@import "abstracts/functions";
@import "abstracts/vars";

html {
	line-height: 1.15; /* 1 */
	-webkit-text-size-adjust: 100%; /* 2 */
	width: 100%;
	margin: 0 auto;
}

body {
	margin: 0 auto;
	font-size: 16px;
	font-family: $mainFont;
	background-color: #fff;
	overflow-x: hidden !important;
	font-weight: 500;
	position: relative;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	padding: rem(120) 0 0;

	@media(max-width: 991px) {
		padding: rem(80) 0 0;
	}

	@media(max-width: 744px) {
		padding: rem(50) 0 0;
	}

	&.no-scroll {
		overflow: hidden;
	}

